body {
  margin: 0 auto;
  padding: 0;
  min-width: 320px;
  font-family: "PT Serif", "Times New Roman", serif;
  font-size: 18px;
  line-height: 30px;
  font-weight: 400;
  color: #22222e;
  background-color: #ffffff; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  width: 1200px;
  margin: 0 auto;
  padding: 0 15px; }

@media (max-width: 1200px) {
  .container {
    width: 100%;
    padding: 0 23px; } }

@media (max-width: 767px) and (min-width: 319px) {
  .container {
    padding: 0 21px 0 14px; } }

.header {
  padding-top: 9px;
  padding-bottom: 9px;
  background-color: #1c1c2a;
  color: #ad9c8c; }

.header__wrapper {
  display: flex;
  align-items: center; }

.header__wrapper-mobile {
  display: none; }

.header__social-list,
.header__menu-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap; }

.header__social-list {
  width: 105px;
  margin-right: 93px; }

.header__social-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 33%; }

.header__social-link {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.header__social-icon {
  fill: #ad9c8c; }
  .header__social-icon--vk {
    width: 22px;
    height: 22px; }
  .header__social-icon--fb {
    width: 36px;
    height: 18px; }
  .header__social-icon--instagram {
    width: 53px;
    height: 15px; }
  .header__social-icon:focus, .header__social-icon:hover {
    fill: #ffffff; }

.header__menu-list {
  width: 273px;
  font-size: 14px;
  line-height: 22px; }

.header__menu-item a {
  padding: 10px 0;
  font: inherit;
  color: #ad9c8c; }
  .header__menu-item a:focus, .header__menu-item a:hover {
    color: #ffffff; }

.header__phone {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 140px;
  margin-left: auto; }

.header__phone-icon {
  width: 14px;
  height: 14px;
  fill: #ad9c8c; }

.header__phone:hover .header__phone-icon,
.header__phone:focus .header__phone-icon {
  fill: #ffffff; }

.header__phone:hover .header__phone-link,
.header__phone:focus .header__phone-link {
  color: #ffffff; }

.header__phone-link {
  width: 116px;
  font-size: 16px;
  line-height: 22px;
  color: #ad9c8c; }

@media (max-width: 1200px) {
  .header__social-list {
    width: 105px;
    margin-right: 6%; }
  .header__menu-list {
    width: 273px; }
  .header__phone {
    width: 139px; } }

@media (max-width: 767px) and (min-width: 319px) {
  .header {
    padding-top: 16px;
    padding-bottom: 9px; }
  .header__wrapper-desktop {
    display: none; }
  .header__wrapper-mobile {
    display: flex;
    align-items: center;
    justify-content: space-between; }
  .header__button {
    padding: 0;
    border: none;
    width: 20px;
    background-color: #1c1c2a; }
  .header__button-icon {
    width: 20px;
    height: 22px;
    fill: #ad9c8c; }
    .header__button-icon:focus, .header__button-icon:hover {
      fill: #ffffff; }
  .header__user-list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 79px; }
  .header__user-item {
    position: relative;
    display: flex;
    justify-content: space-between;
    width: 25%; }
  .header__search-input {
    display: none; }
  .header__user-icon {
    fill: #ffffff; }
    .header__user-icon--search {
      width: 20px;
      height: 20px; }
    .header__user-icon--shopping-bag {
      position: relative;
      width: 20px;
      height: 21px; }
    .header__user-icon:focus, .header__user-icon:hover {
      fill: rgba(255, 255, 255, 0.5); }
  .header__user-number {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    top: -9px;
    left: 60%;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 2px solid #1c1c2a;
    font-size: 10px;
    line-height: 10px;
    font-weight: 700;
    background-color: #d8c58d;
    text-align: center;
    color: #5f4f40; } }
